@use '../layout' as *;
@use '../abstracts' as *;

////
/// Page
/// @group base
////
html {
    background-color: $color-nearblack;
    color: $color-purewhite;
    box-sizing: border-box;
    min-height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    scroll-behavior: smooth;
    
    @include media('reducemotion') {
        scroll-behavior: auto;
    }   
}

body {
    min-height: 100vh;
    position: relative;
    scroll-behavior: smooth;
    @include media('reducemotion') {
        scroll-behavior: auto;
    }   
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

.logo {
    display: block;
    width: $measure--medium7;
}

@mixin default-margin() {
    margin-left: $measure--basic;
    margin-right: $measure--basic;
    max-width: $width--max;

    @include media('>between') {
        margin-left: $measure--small2;
        margin-right: $measure--small2;
    }
        
}

@mixin default-padding() {
    padding-left: $measure--small2;
    padding-right: $measure--small2;

    @include media('>between') {
        padding-left: $measure--small4;
        padding-right: $measure--small4;
    }
        
}

@mixin standard-box() {
    @include default-margin();
    @include default-padding();

    @include media('>large') {
        margin-left: auto;
        margin-right: auto;
    }     
}

main {
    article {
        background-color: $color-purewhite;
        grid-column: 1/-1;
        height: max-content;

        .content-wrapper {
            @include standard-grid();
        }

        .content {
            grid-column: 1/-1;
            padding: $measure--half;
            @include default-margin();
            
            @include media('>=small') {
                grid-column: 2/-2;
                margin: 0;
            }
        }
    }

    .navigation--secondary {
        grid-column: 1 / span 6;

        & + article {
            grid-column: 7 / span 18;
        }
    }
}

.js {
    .nojs-only {
        display: none;
    }
}