
@use '../../abstracts' as *;
@use '../../base' as *;
@use '../../layout' as *;

.global-footer {
    background: $color-nearblack;
    color: $color-purewhite;
    border: thin solid white;
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;

    @include media('>=small') {
        @include standard-box();
        @include x-column-grid(24);
    }
  
}

.global-footer__container {
    padding-bottom: $measure--small4;
        @include default-padding();
        

}