@use '../abstracts' as abs;

////
/// Reset
/// @group base
////
html {
    text-size-adjust: 100%;
}

body {
    margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
figure,
ol,
ul,
dl,
dt,
dd,
fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

main {
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
}

b,
strong {
    font-weight: abs.$font-weight-bold;
}

a,
button {
    color: inherit;
}

a {
    text-decoration: none;
}

button {
    background: none;
    border: 0;
    cursor: pointer;
    font: inherit;
    letter-spacing: inherit;
    overflow: visible;
    padding: 0;
    touch-action: manipulation;
}

label,
select {
    cursor: pointer;
}

::-moz-focus-inner {
    padding: 0;
    border: 0;
}

img {
    max-width: 100%;
    height: auto;
    border: 0;
}

iframe {
    display: block;
    margin: abs.$measure--basic;
}

address {
    font-style: inherit;
}
