// Temporary commented out due to deprecated code within fontawesome.

/// @type List
$font-family-meta: 'ff-meta-web-pro', sans-serif !default;
/// @type List
$font-family-plex-sans: 'ibm-plex-sans', sans-serif !default;
/// @type List
$font-family-plex-serif: 'ibm-plex-serif', serif !default;
/// @type List
$font-family-plex-mono: 'ibm-plex-mono', monospace !default;



/// @type List
$font-family-fontawesome: 'Font Awesome 6 Pro' !default;
/// @type List
$font-family-fontawesome-brands: 'Font Awesome Brands' !default;
/// @type List
$font-family-fontawesome-duotone: 'Font Awesome 6 Duotone' !default;


/// @type List
$font-family-sans: $font-family-plex-sans !default;
/// @type List
$font-family-serif: $font-family-plex-serif !default;
/// @type List
$font-family-mono: $font-family-plex-mono !default;
/// @type List
$font-family-icon: $font-family-fontawesome !default;
/// @type List
$font-family-brands: $font-family-fontawesome-brands !default;
/// @type List
$font-family-duotone: $font-family-fontawesome-duotone !default;
/// @type List
$font-family-head: $font-family-meta !default;
/// @type List
$font-family-body: $font-family-sans !default;


/// @type Map
/// @prop {String} key - Font Family name
/// @prop {Number} value - Font Family value
/// @require {Variable} $font-family-body
/// @require {Variable} $font-family-head
/// @require {Variable} $font-family-serif
/// @require {Variable} $font-family-icon
/// @require {Variable} $font-family-small
/// @require {Variable} $font-family-mono
/// @require {Variable} $font-family-duotone
/// @see {Mixin} font-family
$font-families: (
    'default': $font-family-body,
    'heading': $font-family-head,
    'serif': $font-family-serif,
    'mono': $font-family-mono,
    'icon': $font-family-icon,
    'brands': $font-family-brands,
    'duotone': $font-family-duotone,
) !default;



/// @type Length
$font-size--mobile: 16px !default;
/// @type Length
$font-size--desktop: 18px !default;
/// @type Length
$font-size--1080p: 22px !default;
/// @type Length
$font-size--4K: 46px !default;
/// @type Length
$font-size--8K: 92px !default;

/// @type Length
$max-line-length: 55ch !default;

/// @type Length
$font-size--basic: 1rem !default;

///@type Length
$font-size--icon: 0.875em !default;

/// @type Number
$font-size-dp: 3 !default;
/// @type Number
$font-size--ratio: math.div(9, 8) !default;
/// @type Length
$font-size--alpha: $font-size--basic !default;
/// @type Length
$font-size--beta: decimal-round(
    ($font-size--alpha * $font-size--ratio),
    $font-size-dp
) !default;
/// @type Length
$font-size--gamma: decimal-round(
    ($font-size--beta * $font-size--ratio),
    $font-size-dp
) !default;
/// @type Length
$font-size--delta: decimal-round(
    ($font-size--gamma * $font-size--ratio),
    $font-size-dp
) !default;
/// @type Length
$font-size--epsilon: decimal-round(
    ($font-size--delta * $font-size--ratio),
    $font-size-dp
) !default;
/// @type Length
$font-size--zeta: decimal-round(
    ($font-size--epsilon * $font-size--ratio),
    $font-size-dp
) !default;
/// @type Length
$font-size--eta: decimal-round(
    ($font-size--zeta * $font-size--ratio),
    $font-size-dp
) !default;
/// @type Length
$font-size--theta: decimal-round(
    ($font-size--eta * $font-size--ratio),
    $font-size-dp
) !default;
/// @type Length
$font-size--smalltype: decimal-round(
    calc($font-size--alpha / $font-size--ratio),
    $font-size-dp
) !default;
/// @type Length
$font-size--extrasmalltype: decimal-round(
    calc($font-size--smalltype / $font-size--ratio),
    $font-size-dp
) !default;
/// @type Length
$font-size--sub: decimal-round(
    calc($font-size--extrasmalltype / $font-size--ratio),
    $font-size-dp
) !default;

$font-size--super: 0.7em;

/// @type Length
$font-size--pathway-yt-icon: $font-size--basic * 5;

/// @type Number
$line-height-ratio--heading: calc(8/6) !default;
/// @type Number
$line-height-ratio--body: calc(13/8) !default;
/// @type Length
$line-height--sub: strip-unit($font-size--sub) !default;

/// @type Number
$line-height-ratio--override--alpha: strip-unit($font-size--alpha) !default;
/// @type Number
$line-height-ratio--override--beta: strip-unit($font-size--beta) !default;
/// @type Number
$line-height-ratio--override--gamma: strip-unit($font-size--gamma) !default;
/// @type Number
$line-height-ratio--override--delta: strip-unit($font-size--delta) !default;
/// @type Number
$line-height-ratio--override--epsilon: strip-unit($font-size--epsilon) !default;
/// @type Number
$line-height-ratio--override--zeta: strip-unit($font-size--zeta) !default;




@mixin alphatype() {
    font-size: $font-size--alpha;
    line-height: $line-height-ratio--body;
    letter-spacing: #{dynamic-tracking($font-size--alpha, $font-size--mobile)}em;

    @include media('>=desktop', '<1080p') {
        & {
            letter-spacing: #{dynamic-tracking(
                    $font-size--alpha,
                    $font-size--desktop
                )}em;
        }
    }

    @include media('>=1080p', '<4K') {
        & {
            letter-spacing: #{dynamic-tracking($font-size--alpha, $font-size--1080p)}em;
        }
    }

    @include media('>=4K', '<8K') {
        & {
            letter-spacing: #{dynamic-tracking($font-size--alpha, $font-size--4K)}em;
        }
    }

    @include media('>=8K') {
        & {
         letter-spacing: #{dynamic-tracking($font-size--alpha, $font-size--8K)}em;
        }
    }
}
.alpha-type {
    @include alphatype();
}
