    }
}

@mixin heading--centered($color) {
    text-align: center;

    &:after {
        background-color: $color;
        content: '';
        display: block;
        height: $measure--quarter;
        margin-left: auto;
        margin-right: auto;
        margin-top: $measure--half;
        width: $measure--large2;
    }
}