////
/// Grid
/// @group layout
////
@use '../abstracts' as *;

@mixin x-column-grid($columns: 24) {
    grid-template-columns: repeat($columns, 1fr);
    grid-column-gap: 0;

    @include media('>=tablet') {
        grid-column-gap: $measure--basic;
    }

    @include media('>between') {
        grid-column-gap: $measure--small2;
    }
}

@mixin standard-grid-columns-only() {
    @include x-column-grid(24);
}

@mixin standard-grid() {
    display: grid;
    @include x-column-grid(24);
    
}

@mixin content-grid-columns-only() {
    @include x-column-grid(18);
}

@mixin content-grid() {
    display: grid;
    @include x-column-grid(18);
    
}

@mixin eightcolumn-sub-grid() {
    display: grid;
    grid-row-gap: 0;

    @include media('<=mobile') {
        grid-column-gap: $measure--basic;
    }

    @include media('>mobile', '<=between') {
        grid-column-gap: $measure--basic;
    }

    @include media('>between') {
        grid-column-gap: $measure--small2;
    }

    @include media('<tablet') {
        grid-template-columns: repeat(4, 1fr);
    }

    @include media('>=tablet') {
        grid-template-columns: repeat(8, 1fr);
    }
}

@mixin ninetyten-grid() {
    display: grid;
    grid-template-columns: 9fr 1fr;
    grid-column-gap: $measure--basic;
    grid-row-gap: $measure--basic;
}

%ninetyten-grid {
    @include ninetyten-grid();
}

@mixin standard-grid-wrapper() {
    @include standard-grid();
    @include default-padding();
}

.standard-grid-wrapper {
    display: grid;
    @include x-column-grid(18);
    
}

// sixteen-grid() is to be used inside standard grid with 1-column
// space on each side to its parent element on 'desktop' width -as on 'case-study' PL
@mixin sixteen-grid() {
    display: grid;
    grid-row-gap: 0;

    @include media('<=mobile') {
        grid-column-gap: $measure--basic;
        grid-template-columns: repeat(6, 1fr);
    }

    @include media('>mobile', '<=between') {
        grid-column-gap: $measure--basic;
        grid-template-columns: repeat(12, 1fr);
    }

    @include media('>between') {
        grid-column-gap: $measure--small2;
        grid-template-columns: repeat(16, 1fr);
    }
}
