@use 'measurements' as *;
////
/// Variables
/// @group abstracts
////
/// @type Length
$width--tiny: 375px !default;
/// @type Length
$width--mobile: 432px !default;
/// @type Length
$width--tablet: 768px !default;
/// @type Length
$width--between: 900px !default;
/// @type Length
$width--small: 1024px !default;
/// @type Length
$width--desktop: 1280px !default;
/// @type Length
$width--large: 1440px !default;
/// @type Length
$width--1080p: 1920px !default;
/// @type Length
$width--4K: 3840px !default;
/// @type Length
$width--8K: 7680px !default;

$breakpoints: (
    'tiny': $width--tiny,
    'mobile': $width--mobile,
    'tablet': $width--tablet,
    'between': $width--between,
    'small': $width--small,
    'desktop': $width--desktop,
    'large': $width--large,
    '1080p': $width--1080p,
    '4K': $width--4K,
    '8K': $width--8K,
) !default;

$width--max: 80rem !default;

$media-expressions: (
    'hover': '(hover: hover)',
    'nohover': '(hover: nohover)',
    'print': 'print',
    'reducemotion': '(prefers-reduced-motion: reduce)',
    'screen': 'screen',
) !default;

/// @type String
$font-path: '/fonts/' !default;
$fa-font-path: "../fonts/"; //Override the default font path for font awesome.

/// @type Number
$font-weight-hairline: 100 !default;
/// @type Number
$font-weight-thin: 200 !default;
/// @type Number
$font-weight-light: 300 !default;
/// @type Number
$font-weight-normal: 400 !default;
/// @type Number
$font-weight-text: 400 !default;
/// @type Number
$font-weight-book: 500 !default;
/// @type Number
$font-weight-medium: 600 !default;
/// @type Number
$font-weight-bold: 700 !default;
/// @type Number
$font-weight-black: 800 !default;
/// @type Number
$font-weight-extrablack: 900 !default;

/// @type Length
$transition-duration--instant: 0.001s !default; // use instead of transition: none if you're relying on a transition event firing
/// @type Length
$transition-duration--short: 0.125s !default;
/// @type Length
$transition-duration: 0.2s !default;
/// @type Length
$transition-duration--medium: 0.4s !default;
/// @type Length
$transition-duration--long: 0.6s !default;
/// @type String
$transition-function: ease-in-out !default;

/// @type String
$border-style-solid: solid !default;
/// @type String
$border-style-dotted: dotted !default;
/// @type String
$border-style-dashed: dashed !default;

/// @type Number
$lightness-percentage: 30% !default;

// fat finger touch targets
/// @type Length
$minimum-control-height: $measure--medium3 !default;

/// @type Length
$heading-margin: $measure--basic !default;
/// @type Length
$content-element-margin: $measure--threequarters !default;

//mobile banner overlay max height
$banner-mobile-image-max-height: 320px !default;

//banner overlay max height tablet
$banner-tablet-image-max-height: 480px !default;

//banner overlay max height between
$banner-between-image-max-height: 280px !default; // tablets in horizontal

//banner overlay max height desktop
$banner-desktop-image-max-height: 380px !default;

//banner overlay max height 4k
$banner-4k-image-max-height: 900px !default;

//banner overlay max height 8k and above
$banner-8k-image-max-height: 1800px !default;

/// @type Map
/// @prop {String} key - z-index name
/// @prop {Number} value - z-index value
/// @see {Function} z-index
$z-indexes: (
    'below': -1,
    'default': 0,
    'above': 1,
    'inpage': 2,
    'veil': 3,
    'unveiled': 4,
    'navigation': 5,
    'modal': 6,
) !default;

/// @type Length
$card-third-max-image-height: 220px !default;
/// @type Length
$card-half-max-image-height: 280px !default;
