@use '../abstracts' as *;
@use './typography' as *;

////
/// Content
/// @group base
////
html {
    // @include font-family('default');
    // @include body-font-features();

    font-size: $font-size--mobile;
    font-style: normal;
    font-weight: $font-weight-text;
    line-height: 1;
    text-rendering: optimizeLegibility;
    @include font-family('default');

    @include media('>=desktop', '<1080p') {
        font-size: $font-size--desktop;
    }

    @include media('>=1080p', '<4K') {
        font-size: $font-size--1080p;
    }

    @include media('>=4K', '<8K') {
        font-size: $font-size--4K;
    }

    @include media('>=8K') {
        font-size: $font-size--8K;
    }
}


u {
    text-decoration: none;
}

h1, h2,h3,h4,h5 {
    -webkit-font-smoothing: antialiased;
}
.sans {
    font-weight: $font-weight-text;
    @include font-family('default');


    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

.serif {
    font-weight: $font-weight-text;
    @include font-family('serif');


    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

.mono {
    font-weight: $font-weight-text;
    @include font-family('mono');


    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

.meta {
    font-weight: $font-weight-normal;
    @include font-family('heading');
    @include heading-font-features();



    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

.weight-hairline {
    font-weight: $font-weight-hairline;
}

.weight-thin {
    font-weight: $font-weight-thin;
}

.weight-light {
    font-weight: $font-weight-light;
}

.weight-normal {
    font-weight: $font-weight-normal;
}

.weight-text {
    font-weight: $font-weight-text;
}

.weight-book {
    font-weight: $font-weight-book;
}

.weight-medium {
    font-weight: $font-weight-medium;
}

.weight-bold {
    font-weight: $font-weight-bold;
}

.weight-black {
    font-weight: $font-weight-black;
}

.weight-extrablack {
    font-weight: $font-weight-extrablack;
}

.style-fraction {
    @include body-fraction-font-features();
}

@mixin body-baseline() {
    font-style: normal;
    font-weight: $font-weight-text;
    @include font-family('default');
    & {
        @include alphatype();
    }
    
    & {
        @include body-font-features();
    }
    
    & {
        b,
        strong {
            font-weight: $font-weight-bold;
        }

        i,
        em {
            font-style: italic;
        }
    }

    
}

@mixin content-style() {
    & {
        max-width: $max-line-length;
        @include body-baseline();
    }

    
}

.content-style {
    @include content-style();
}

@mixin mono-style() {
    @include font-family('mono');
    @include content-style();
}

code,
pre {
    letter-spacing: 0;
    max-width: unset;
    @include mono-style();
}

p code {
    background: $color-gray9;
    border-radius: $measure--quarter;
    color: $color-purewhite;
    padding-left: $measure--quarter;
    padding-right: $measure--quarter;
}

h6,
.h6-style {
    @include h6style();
}

h5,
.h5-style {
    @include h5style();
}

h4,
.h4-style {
    @include h4style();
}



h3,
.h3-style {
    @include h3style();
}



h2,
.h2-style {
    @include h2style();
}

h1,
.h1-style {
    @include h1style();
}

h1,
h2,
h3,
h4,
h5,
h6 {
    & + h2,
    & + h3,
    & + h4,
    & + h5,
    & + h6 {
        margin-top: $heading-margin;
    }
}

hr {
    border-top: $border-width-hairline $border-style-dotted $color-gray12;
    border-bottom: 0;
}

@mixin standfirst() {
    font-style: normal;
    font-weight: $font-weight-light;
    line-height: $line-height-ratio--body;
    margin-top: $content-element-margin;
    max-width: $max-line-length;
    @include font-family('heading');
    @include heading-font-features();
    @include gammatype();
}


main {
    position: relative; // To stop full-page background fill on Finder pattern

    p,
    ol,
    ul,
    dl,
    blockquote {
        @include content-style();
    }
    p,
    ol,
    ul,
    dl,
    blockquote,
    figure {
        margin-top: $content-element-margin;
    }

    sup,
    sub {
        font-size: $font-size--super;
        line-height: $font-size--super;
    }

    header {
        p {
            font-weight: $font-weight-light;
            margin-top: 0;
            @include h2style();
        }
    }

    .standfirst,
    .standfirst-style {
        @include standfirst();
    }

    p > img {
        margin-top: $content-element-margin;
    }

    ol,
    ul {
        margin-left: $measure--medium1;
        max-width: calc(#{$max-line-length} - #{$measure--medium1});
    }

    dl {
        max-width: $max-line-length;
    }

    ul {
        list-style: disc;

        ul {
            list-style: circle;
        }
    }

    dt {
        @include h6style();

        &:first-of-type {
            margin-top: 0;
        }
    }

    li {
        margin-top: $content-element-margin;
    }

    table {
        display: block;
        margin-top: $content-element-margin;
        min-width: 66%;
        max-width: 100%;
        overflow-x: scroll;
        border-collapse: collapse;
        font-weight: $font-weight-text;

        tr {
            th,
            td {
                border: $border-width-hairline $border-style-solid $color-gray3;
                vertical-align: top;
                &:first-child {
                    text-align: left;
                }
                &:not(:first-child) {
                    text-align: right;
                }
            }
        }

        th,
        strong {
            font-weight: $font-weight-medium;
        }

        thead {
            tr {
                color: $color-purewhite;
                &:first-child {
                    th {
                        background: $color-pink90;
                    }
                }

                &:nth-child(2) {
                    th {
                        background: $color-pink80;
                    }
                }

                .heading__secondary {
                    background: $color-pink80;
                }
            }
            td,
            th {
                padding: $measure--basic $measure--half;
                vertical-align: middle;
            }
        }

        tbody {
            color: $color-gray10;
            background: $color-purewhite;
            tr {
                &:hover {
                    background-color: $color-gray2;
                }
            }
            th,
            td {
                padding: $measure--half;
                vertical-align: bottom;
            }
        }
    }
}

/* Banned embeds – these are not accessible so cannot be allowed on the website. */
iframe {
    &[src*='issuu'],
    &[src*='prezi'] {
        display: none !important;
    }
}